import './utils/api/api.connect';
import '../popup/enableDevHmr';
import renderApp from './features/app/views/App/App.view';
import logger from '~B/utils/logger';

// const packageJson = require('../../package.json');
logger.log('ui env vars', import.meta.env);
renderApp(import.meta.PLUGIN_WEB_EXT_CHUNK_CSS_PATHS);

cholloExtensionDetection();

async function cholloExtensionDetection() {
    if (location.host.match('www.chollometro.com')) {
        const meta = document.createElement('meta');
        meta.name = 'chollometro-extension';
        meta.setAttribute('version', (await import('../manifest')).default.version);
        document.head.appendChild(meta);
    }
}
